
#root{
  position: absolute;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  z-index: -100;
}
.square{
  float: left;
}
.square-red{
  background-color: red;
  border-radius: 50%;
  
  border-bottom: 3px solid black;
}
.square-orange{
  
  border-radius: 50%;
  background-color: orange;
  
  border: 3px solid black;
}
.square-border{
  border-radius: 50%;
  border-bottom: 10px solid;
  border-top: 4px solid;
  border-right: 4px solid;
  border-left: 4px solid;
}
.border-orange{
  border-color: orange;
}

.border-yellow{
  border-color: yellow;
}

.border-green{
  border-color: green;
}

.border-blue{
  border-color: blue;
}

.border-red{
  border-color: red;
}
.square-green{
  
  border-radius: 50%;
  background-color: green;
  border-bottom: 3px solid black;
}
.square-yellow{
  
  border-radius: 50%;
  background-color: yellow;
  border-bottom: 3px solid black;
}
.square-blue{
  
  border-radius: 50%;
  background-color: blue;
  border-bottom: 3px solid black;
}
.square-outer{
  border-radius: 50%;
  border-bottom: 5px solid white;

}
.square-white{
  
  border-radius: 50%;
  float:none;
  border: 3px solid black;
  background-color: white;
}

.bg-white{
  background-color: white;
}
.trapezoid {
  position: absolute;
  left: 25vw;
  top:100px;
  border-bottom: 30vw solid rgb(156, 156, 156);
  border-left: 20vw solid transparent;
  border-right: 20vw solid transparent;
  height: 0;
  z-index: -1;
}
.t-black{
  border-bottom-color: black;
  z-index: -2;
  border-bottom-width: calc(30vw + 5px);
  left: calc(25vw - 5px);
}
.t-shadow{
  z-index: -5;
  top:100px;
  border-bottom: 20vw solid rgba(0, 0, 0, 0.068);
  border-left: 23vw solid transparent;
  border-right: 23.5vw solid transparent;
  border-bottom-width: calc(32vw + 5px);
  left: calc(22vw - 5px);
}

img{
  position:absolute;
  top:0;
  left:calc(50vw - 140px);
}

#background{
  position:absolute;
  background: linear-gradient(to bottom, #fffb00, #ffa247);
  z-index: -10;
}

audio{
  position:absolute;
  z-index: 100;
}

.rock-meter-outer{
  width: 15vw;
  height: 12vw;
  border-top: black solid 15px;
  border-radius: 25%;
  background: linear-gradient(to bottom, rgb(88, 88, 88) , lightgray 40%, lightgray 85%, rgb(173, 173, 173));
  border-bottom: black solid 10px;
  border-left: solid black 7.5px;
  border-right: solid black 5px;
}
.rock-meter-shadow{
  width: 15.5vw;
  height: 12.25vw;
  border-radius: 25%;
  background-color: rgba(0, 0, 0, 0.062); 
}
.rock-meter-square{
  position:absolute;
  width: 30%;
  height: 20%;
  top:15%;
  border-radius: 5%;
  border: solid black 2px; 
}
.rock-meter-green{
  left: 67%;
  background: radial-gradient(#00ff40, #00ff40 55%, #009225);
}
.rock-meter-red{
  left: 3%;
  background: radial-gradient(red, red 55%, rgb(114, 0, 0));
}
.rock-meter-yellow{
  left:35.25%;
  background: radial-gradient(yellow, yellow 55%, rgb(155, 155, 0)); 

}
.rock-meter-square-shadow{
  background: linear-gradient(rgba(0,0,0, 40%), rgba(0, 0, 0, 0) 20%);
}

.rock-meter-square-shadow-bottom{
  background: linear-gradient(to top, rgba(0,0,0, 30%), rgba(0, 0, 0, 0) 20%);
}
.rock-img{
  position: absolute;
  top: 40%;
  left: 16.5%;
  width:65%;
  height: 40%;
}
.rock-meter-circle{
  position: absolute;
  border-radius: 50%;
  width: 35%;
  height: 30%;
  top:90%;
  left:32.5%;
  background-color: black;
  z-index:2;
}
.rock-meter-circle-outer{
  position: absolute;
  border-radius: 50%;
  border-right: 4px solid black;
  border-top: 4px solid black;
  border-left: 4px solid black;
  width: 50%;
  height: 50%;
  top:83%;
  left:25%;
  background: radial-gradient(white, rgb(155, 155, 155));
  z-index:1;
}

.rock-meter-stick{
  position:absolute;
  background-color: black;
  width: 5px;
  top:10%;
  left:calc(50% - 2.5px);
  height:160%;
  z-index:3;
  transform: rotate(0deg);

}

